<template>
  <div class="container">
        <div class="row my-5 justify-content-center">
            <div class="col-12 col-md-4">

                <div class="">

                    <Title title="Vue Todo List"></Title>

                    <ListCreate @create="create"></ListCreate>

                    <div class="d-flex justify-content-between align-items-center">
                        <p class="font-weight-bold">
                            <!-- ToDO List{{ lists.length > 1 ? "s" : "" }}  -->
                            <BaseText> Todo List{{ lists.length > 1 ? "s" : "" }} </BaseText>
                        </p>
                        <p v-if="lists.length > 0 && lists.length === doneTotal" class="badge badge-success badge-pill">
                            Well Done <i class="fas fa-check-circle"></i>
                        </p>
                        <p v-else class="badge badge-danger badge-pill">
                            Done {{ doneTotal }}
                        </p>
                    </div>
                    

                    <ul class="list-group">

                        <NoList v-if="lists.length === 0" name="Nothing Todo ðŸ˜“"></NoList>

                        <List v-for="list in lists" :key="list.id" :list="list" @del="del"></List>

                    </ul>

 
                </div>  

            </div>
        </div>
    </div>
</template>

<script>
import List from './components/List.vue';
import ListCreate from './components/ListCreate.vue';
import NoList from './components/NoList.vue';
import Title from './components/Title.vue';
  export default {
  components: { Title, List, ListCreate, NoList},
      name : "App",
      data() {
        return {
          currentId : 0 ,          
          lists : [

          ]
        }
      },
      computed : {

          doneTotal(){

              return this.lists.filter(el => el.isDone === true).length ;

          }

      },
      methods : {

          create(x){

              this.currentId++
              this.lists.push({

                  id : this.currentId ,
                  message : x ,
                  isDone : false ,                  
              });
              

          },
          del(x){

              // if(confirm('Are U sure to Delete ?')){

                  setTimeout(() => {

                      this.lists = this.lists.filter(el => el.id !== x  )

                  }, 600);

              // } 

          }

      }
  }
</script>

<style>

      .done{
          text-decoration: line-through !important;
          animation: bounce 0.5s;
      }
      .created{
          animation: fadeInDown 0.5s;
      }
      .deleted{
          animation: flash 0.5s;
      }

</style>