<template>
    <div class="form-row mb-4">
        

        <div class="col-10">
            <input type="text" v-model="newMessage" placeholder="Say Something" class="form-control form-control-lg" @keyup="showError=false"  @keyup.enter="create()">
        </div>
        <div class="col-2">
            <button class="btn btn-lg btn-danger" @click="create()">
                <i class="fas fa-plus-circle"></i>
            </button>
        </div>
        <div class="col-12">
            <small v-if="showError" class="text-danger font-weight-bold">{{ errorMessage }}</small>
        </div>

    </div>
</template>

<script>
    export default {
        name : "ListCreate",
        data() {
            return {
                newMessage: "",
                showError : false,
                errorMessage : "",
            }
        },
        methods: {
            create() {
                if(this.newMessage.length){

                    this.showError = false ;
                    this.$emit("create", this.newMessage);
                    this.newMessage = "" ;

                }else{

                    this.showError = true ;
                    this.errorMessage = "Write Something"

                }
                
            }
        },
    }
</script>

